import type { MessageTrigger } from './message-trigger.enum';

export type IncomingMessage = Record<string, any>;

export type MessageInfo = {
  trigger: Trigger[];
};

export interface TriggerExtra {
  arn?: string;
  resource?: string;
  httpMethod?: string;
  api?: string;
  stage?: string;
  approxEventCreationTime?: number;
}

export interface Trigger {
  id: string;
  targetId: string | null;
  triggeredBy: MessageTrigger;
  fromMessageIds: string[];
  extra?: TriggerExtra;
}
